import React from 'react';
import Footer from '../components/Footer';
import Header from '../components/Header';
import { Main, Wrapper } from './styles';


const Layout = ({children, page}) => {

  return(
    <Wrapper>
        <Header page={page}/>
        <Main>
          {children}
        </Main>
        <Footer />
    </Wrapper>
  )
  ;
}

export default Layout;