import React, { useState, useContext, useEffect } from "react";
import { useParams } from "react-router-dom";

import { AuthContext } from "../../auth/AuthContext";
import * as S from "./styles";
import firebase from "firebase"
import CardCompartilhar from "../../components/CardCompartilhar";

// import RoundedImage from '../../components/RoundedImage';
// import image from '../../assets/clarisse.png'
import Store from "../../Connections/store";
import Header from "../../components/Header";
import Footer from "../../components/Footer";
import { useHistory } from "react-router-dom";
import Share from '../../components/CardCompartilhar/index'
export default function ComponenteVideo(props) {


    const history = useHistory()
    const [mostar, setMostrar] = useState(false)
    const [visualicacao, setvisualicacao] = useState(0)
    const [compartilhar, setcompartilhar] = useState(false)
    const params = useParams('/playespetaculo/:id/:id2')
    const { id, id2 } = params
    console.log("id" + id)
    console.log("id2" + id2)
    console.log(params)



    useEffect(() => {


        setTimeout(() => {
            setvisualicacao(1)
        }, 10000)

    }, [])

    const logado = localStorage.getItem("logado");
    
    if(logado != 'true'){
        history.push('/login')
    }


    return (
        <div style={{ width: '100%', display: "flex", padding: 0 }}>

            <S.div >



                <S.Span size="35" style={{ cursor: "pointer" }} onClick={() => history.push('/')}><i class="bi bi-arrow-left"></i></S.Span>
                <S.Span>Visualizaçoes: {visualicacao}</S.Span>
                <S.Span size="30" style={{ cursor: "pointer" }} ><i onClick={() => compartilhar ? setcompartilhar(false) : setcompartilhar(true)} class="bi bi-share"></i> {compartilhar && <Share link={`https://palcoweb.com.br/#/playespetaculo/${id}/${id2}`}></Share>}</S.Span>





            </S.div>


            <iframe 
            src={`https://iframe.dacast.com/vod/${id}/${id2}`} 
            style={{ position: "absolute", marginTop: '0px', padding: 0, width: '100%', height: '800px' }} 
            allow="autoplay" 
            allowfullscreen 
            allowfullscreen="true" 
            webkitallowfullscreen="true" 
            mozallowfullscreen="true"
            
            >


            </iframe>



        </div>
    )
}